ul {
  list-style-type: none;
  /* margin: 0; */
  /* margin: auto; */
  padding: 0;
  overflow: hidden;
  background-color: #333;
  /* margin-top: ; */
  display: flex;
  justify-content: center;
  align-items: center;
}

li {
  float: left;
  
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  background-color: #111;
}
table {
    border-collapse: collapse;
    width: 50%;
    margin: auto;
  }
  
  td, th {
    border: 1px solid black;
    text-align: left;
    padding: 10px;
  }
  a{
    text-decoration: none;
  }

  td{
    text-align: center;
  }

  .side{
    float:center;
  }